matchingRoutes.patch('/settings', async (c) => {
  const userId = c.get('userId');
  if (!userId) throw new AppError('User not found in context', 500, 'CONTEXT_MISSING_USER');
  const updates = await c.req.json().catch(() => ({}));
  const current = await getMatchingSettings(c.env, userId);
  const merged = {
    ...current,
    ...updates,
    updatedAt: new Date().toISOString()
  };
  await c.env.CACHE.put(`matching:settings:${userId}`, JSON.stringify(merged));
  return successResponse(c, merged);
});

// AI 바인딩 테스트 엔드포인트 (디버깅용)
matchingRoutes.get('/ai/test', async (c) => {
  try {
    const hasAI = !!c.env.AI;
    const envKeys = Object.keys(c.env);

    if (!hasAI) {
      return successResponse(c, {
        status: 'error',
        message: 'AI binding not found',
        availableBindings: envKeys,
        environment: c.env.ENVIRONMENT
      });
    }

    // 간단한 임베딩 테스트
    try {
      const testEmbedding = await c.env.AI.run('@cf/baai/bge-base-en-v1.5', {
        text: 'test'
      });

      return successResponse(c, {
        status: 'success',
        message: 'AI binding works',
        hasEmbedding: !!(testEmbedding as any)?.data,
        embeddingLength: (testEmbedding as any)?.data?.[0]?.length || 0,
        environment: c.env.ENVIRONMENT
      });
    } catch (aiError) {
      return successResponse(c, {
        status: 'error',
        message: 'AI binding exists but call failed',
        error: aiError instanceof Error ? aiError.message : String(aiError),
        environment: c.env.ENVIRONMENT
      });
    }
  } catch (error) {
    return successResponse(c, {
      status: 'error',
      message: 'Test endpoint error',
      error: error instanceof Error ? error.message : String(error)
    });
  }
});

// AI-powered matching endpoints
matchingRoutes.post('/ai/best-matches', async (c) => {
  const userId = c.get('userId');
  if (!userId) throw new AppError('User not found in context', 500, 'CONTEXT_MISSING_USER');

  try {
    log.info('[AI_BEST_MATCHES] Request started', { userId });

    // AI 바인딩 확인
    if (!c.env.AI) {
      log.error('[AI_BEST_MATCHES] AI binding not available', undefined, { userId });
      throw new AppError('AI service is not available', 503, 'AI_SERVICE_UNAVAILABLE');
    }

    const body = await c.req.json().catch(() => ({}));
    const limit = typeof body.limit === 'number' ? Math.min(body.limit, 50) : 10;

    log.info('[AI_BEST_MATCHES] Request body parsed', { limit, preferences: body });

    // Get user's profile
    log.info('[AI_BEST_MATCHES] Fetching user profile', { userId });
    const userProfile = await getUserProfile(c.env, userId);
    if (!userProfile) {
      log.error('[AI_BEST_MATCHES] User profile not found', undefined, { userId });
      throw new AppError('User profile not found', 404, 'USER_NOT_FOUND');
    }
    log.info('[AI_BEST_MATCHES] User profile fetched', { userId, profileId: userProfile.id });

    // Get candidate profiles (from existing recommendPartners logic)
    log.info('[AI_BEST_MATCHES] Fetching candidates', { userId });
    const candidates = await recommendPartners(c.env, userId, {
      page: 1,
      size: 100 // Get more candidates for AI to analyze
    });
    log.info('[AI_BEST_MATCHES] Candidates fetched', {
      userId,
      totalCandidates: candidates.total,
      candidateCount: candidates.data.length
    });

    // 후보가 없는 경우 처리
    if (!candidates.data || candidates.data.length === 0) {
      log.warn('[AI_BEST_MATCHES] No candidates found', { userId });
      return successResponse(c, {
        matches: [],
        totalCandidates: 0,
        analyzedCandidates: 0,
        message: '매칭할 파트너를 찾지 못했습니다. 잠시 후 다시 시도해주세요.'
      });
    }

    // Get matching preferences from body or use defaults
    const preferences: MatchingPreferences = {
      languageWeight: typeof body.languageWeight === 'number' ? body.languageWeight : 0.25,
      levelWeight: typeof body.levelWeight === 'number' ? body.levelWeight : 0.15,
      semanticWeight: typeof body.semanticWeight === 'number' ? body.semanticWeight : 0.15,
      scheduleWeight: typeof body.scheduleWeight === 'number' ? body.scheduleWeight : 0.15,
      goalsWeight: typeof body.goalsWeight === 'number' ? body.goalsWeight : 0.10,
      personalityWeight: typeof body.personalityWeight === 'number' ? body.personalityWeight : 0.10,
      topicsWeight: typeof body.topicsWeight === 'number' ? body.topicsWeight : 0.10,
    };

    log.info('[AI_BEST_MATCHES] Preferences set', { preferences });

    // Find best matches using AI
    log.info('[AI_BEST_MATCHES] Starting AI matching', {
      userId,
      candidateCount: candidates.data.length,
      limit
    });
    const matchScores = await findBestMatches(
      c.env.AI,
      userProfile,
      candidates.data,
      preferences,
      c.env,
      limit
    );
    log.info('[AI_BEST_MATCHES] AI matching completed', {
      userId,
      matchCount: matchScores.length
    });

    // MatchScore를 파트너 정보와 함께 매핑
    const matches = matchScores.map((score) => {
      const partner = candidates.data.find(
        (candidate) => ('id' in candidate ? candidate.id : candidate.userId) === score.userId
      );
      
      if (!partner) {
        log.warn('[AI_BEST_MATCHES] Partner not found for match score', { userId: score.userId });
        return null;
      }

      return {
        partner: {
          id: 'id' in partner ? partner.id : partner.userId,
          userId: 'id' in partner ? partner.id : partner.userId,
          name: partner.englishName || partner.name || 'Unknown',
          profileImageUrl: partner.profileImageUrl,
          bio: partner.selfBio,
          age: partner.age,
          gender: partner.gender,
          location: partner.location,
          nativeLanguage: partner.nativeLanguage,
          targetLanguages: partner.targetLanguages || [],
          interests: partner.interests || [],
          personalities: partner.partnerPersonalities || [],
          onlineStatus: partner.onlineStatus || 'OFFLINE',
          lastActiveTime: partner.lastActiveTime
        },
        score: score.overallScore,
        scores: score.breakdown,
        matchReason: score.aiReasons.join(' '),
        conversationStarters: score.suggestedTopics,
        overallScore: score.overallScore,
        compatibilityInsights: score.compatibilityInsights
      };
    }).filter(Boolean); // null 제거

    log.info('[AI_BEST_MATCHES] Matches formatted', {
      userId,
      matchCount: matches.length
    });

    return successResponse(c, {
      matches,
      totalCandidates: candidates.total,
      analyzedCandidates: candidates.data.length,
    });
  } catch (error) {
    // AppError는 그대로 throw (원래 상태 코드 유지)
    if (error instanceof AppError) {
      log.error('[AI_BEST_MATCHES] AppError occurred', error, { userId });
      throw error;
    }

    // 기타 에러는 500으로 래핑 (더 상세한 로깅)
    log.error('[AI_BEST_MATCHES] Unexpected error occurred', error as Error, {
      userId,
      errorMessage: error instanceof Error ? error.message : String(error),
      errorStack: error instanceof Error ? error.stack : undefined,
      errorName: error instanceof Error ? error.name : undefined,
      hasAIBinding: !!c.env.AI,
      envKeys: Object.keys(c.env)
    });

    // 개발/디버깅을 위해 더 상세한 에러 메시지 반환
    const errorDetail = error instanceof Error ? error.message : String(error);
    const errorStack = error instanceof Error ? error.stack : undefined;

    throw new AppError(
      `AI matching failed: ${errorDetail}${errorStack ? '\n' + errorStack.split('\n').slice(0, 3).join('\n') : ''}`,
      500,
      'AI_MATCHING_FAILED'
    );
  }
});

matchingRoutes.post('/ai/compatibility/:partnerId', async (c) => {
  const userId = c.get('userId');
  if (!userId) throw new AppError('User not found in context', 500, 'CONTEXT_MISSING_USER');
  const partnerId = c.req.param('partnerId');

  try {
    const body = await c.req.json().catch(() => ({}));

    // Get both profiles
    const [userProfile, partnerProfile] = await Promise.all([
      getUserProfile(c.env, userId),
      getUserProfile(c.env, partnerId)
    ]);

    if (!userProfile || !partnerProfile) {
      throw new AppError('User or partner profile not found', 404, 'PROFILE_NOT_FOUND');
    }

    // Get matching preferences
    const preferences: MatchingPreferences = {
      languageWeight: typeof body.languageWeight === 'number' ? body.languageWeight : 0.25,
      levelWeight: typeof body.levelWeight === 'number' ? body.levelWeight : 0.15,
      semanticWeight: typeof body.semanticWeight === 'number' ? body.semanticWeight : 0.15,
      scheduleWeight: typeof body.scheduleWeight === 'number' ? body.scheduleWeight : 0.15,
      goalsWeight: typeof body.goalsWeight === 'number' ? body.goalsWeight : 0.10,
      personalityWeight: typeof body.personalityWeight === 'number' ? body.personalityWeight : 0.10,
      topicsWeight: typeof body.topicsWeight === 'number' ? body.topicsWeight : 0.10,
    };

    // Calculate AI-powered compatibility score
    const compatibility = await calculateAIMatchScore(
      c.env.AI,
      userProfile,
      partnerProfile,
      preferences,
      c.env
    );

    return successResponse(c, {
      partnerId,
      compatibility
    });
  } catch (error) {
    // AppError는 그대로 throw (원래 상태 코드 유지)
    if (error instanceof AppError) {
      throw error;
    }

    // 기타 에러는 500으로 래핑
    throw new AppError(
      error instanceof Error ? error.message : 'AI compatibility calculation failed',
      500,
      'AI_COMPATIBILITY_FAILED'
    );
  }
});

export default matchingRoutes;
